@font-face{
    font-family: ifont;
    src:url("./SpaceGrotesk-SemiBold.woff");
}

html{
    font-family: ifont;
    font-size: 16pt;
    line-height: 20pt;
}

body{
    margin: 0;
    overflow: hidden;
}

.root0{
    &::-webkit-scrollbar {
        display: none;
    }
    transition: background-color 0.5s;
    width: 100vw;
    height: calc(100 * var(--vh));
    overflow-y: scroll;
    overflow-x: hidden;
    position: absolute;
    top: 0;
    text-align: center;
    padding-bottom: 1rem;
    box-sizing: border-box;
    .container{
        min-height: calc(100 * var(--vh) - 2rem);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        h1{
            font-size: 20pt;
            margin: 2rem 1rem 0 1rem;
        }
        h2{
            font-size: 16pt;
            margin: 0 1rem 1rem 1rem;
        }
        a{
            text-decoration: none;
            color: black!important;
            display: inline-block;
            .artist{ 
                pointer-events: none;
                max-width: 400px;
                margin: 1rem 1rem 0 1rem;
                width: calc(100vw - 2rem);
                position: relative;
                .hero{
                    &:nth-child(1){
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                    }
                    width: 100%;
                    height: 400px;
                    transition: transform 0.5s, width 0.5s, height 0.5s, opacity 0.3s;
                    position: relative;
                    video{
                        height: 100%;
                        width: 100%;
                        object-fit: cover;
                    }
                    img{
                        top: 0;
                        right: 0;
                        left: 0;
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        z-index: -1;
                    }
                }
                h1{
                    margin: 0;
                    font-size: 16pt;
                    line-height: 16pt;
                    text-transform: uppercase;
                    margin: 0.5rem 1rem 0 1rem;
                }
                h2{
                    margin: 0;
                    font-size: 12pt;
                    line-height: 16pt;
                    margin: 0 1rem 1rem 1rem;
                }
            }
        }
    }  
}

.back{
    text-decoration: none;
    color: black;
    z-index: 1000;
    position: fixed;
    top: 0.5rem;
    left: 0.5rem;
    transition: transform 0.5s;
    div{
        pointer-events: none;
        background-color: rgba(0,0,0,0.5);
        padding: 0.1rem 0.5rem 0 0.5rem;
        border-radius: 1rem;
        color: white;
        backdrop-filter: blur(5px);
    }
}

.sub{
    &.enter{
        transform:translate(0,50%)
    }
    &.enter-active{
        transition: transform 0.5s;
        transform:translate(0,0)
    }
    &.exit{
        transition: transform 0.5s;
        transform:translate(0,100%)
    }
    position: absolute;
    width: 100vw;
    height: calc(100 * var(--vh));
    overflow-x: hidden;
    overflow-y: auto;
    top: 0;
    z-index: 2;
    .hero{
        position: relative;
        width: 100%;
        height: calc(80 * var(--vh));
        h1{
            color: white;
            margin: 1rem;
            font-size: 20pt;
            text-transform: uppercase;
            position: absolute;
            bottom: 0;
        }
    }
    .main{
        min-height: calc(20 * var(--vh));
        line-height: 0;
        background-color: white;
        width: 100%;
        h1{
            font-size: 20pt;
            text-transform: uppercase;
            margin: 2rem 1rem 0 1rem;
            line-height: 20pt;
        }
        .half{
            padding: 1rem 0 0 1rem;
            width: calc(50% - 0.5rem);
            box-sizing: border-box;
            display: inline-block;
        }
        .full{
            padding: 1rem 0 0 1rem;
            width: calc(100% - 1rem);
            box-sizing: border-box;
            display: inline-block;
        }
        .media{
            height: calc(50vw * var(--hw));
            *{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .full.media{
            height: calc(100vw * var(--hw));
        }
        .text{
            text-align: left;
            span{
                font-size: 12pt;
                line-height: 12pt;
                display: inline-block;
                margin: 0 1rem 1rem 0;
                text-align: left;
                vertical-align: bottom;
            }
        }
        .frame{
            iframe{
                width: 100%;
                height: calc(28.125vw - 0.5625rem);
            }
        }
        .sticky{
            margin-top: 1rem;
            width: 100%;
            overflow-x:hidden;
            overflow-y: visible;
            position: relative;
            &::-webkit-scrollbar{
                display: none;
            }
            .horizon{
                pointer-events: none;
                position: absolute;
                top: 0;
                left: 0;
                display: flex;
                height: calc(80 * var(--vh));
                width: calc(80vw * var(--items));
                .card{
                    position: relative;
                    height: 100%;
                    width: 80vw;
                    box-sizing: border-box;
                    .media{
                        width: 100%;
                        height: calc(80 * var(--vh));
                        *{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                    h1{
                        position: absolute;
                        bottom: calc(40 * var(--vh));
                        left: 40vw;
                        transform: translate(-50%,50%);
                        max-width: 80%;
                        padding: 0.3rem 0.5rem 0.4rem 0.5rem;
                        border-radius: 1rem;
                        backdrop-filter: blur(5px);
                        background-color: rgba(0,0,0,0.5);
                        margin: 0;
                        text-align: center;
                        font-size: 1rem;
                        line-height: 1rem;
                        text-transform:lowercase;
                        color: white;
                    }
                }
            }
            .vertical{
                height: calc(80vw * var(--items));
            }
        }
        .about{
            margin-top: 1rem;
            width: 100%;
            height: 400px;
            background-color: black;
            color: white;
            overflow:hidden;
            position: relative;
            .text{
                width: 50vw;
                line-height: 1rem;
                font-size: 12pt;
                *{
                    margin: 200px 1rem 0 1rem;
                    transform: translate(0,-50%);
                }
                a{
                    display: inline-block;
                    margin: 1.5rem 0.5rem 0 0;
                    padding: 0.2rem 0.5rem 0.2rem 0.5rem;
                    border: 0.1rem solid white;
                    color: white !important;
                    text-decoration: none;
                }
            }
            .media{
                position: absolute;
                height:400px;
                left: 50vw;
                top: 0;
                width: 50vw;
                *{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}

@media only screen and (max-width: 1024px) {
    .sub{
        .main{
            .half.text{
                width: 100%;
            }
        }
    }
}

@media only screen and (max-width: 800px) {
    .sub{
        .main{
            .half{
                width: calc(100% - 1rem);
            }
            .frame{
                iframe{
                    height: calc(56.25vw - 1.125rem);
                }
            }
            .media{
                height: calc(100vw * var(--hw));
            }
            .about{
                height: 800px;
                .text{
                    width: 100%;
                }
                .media{
                    top: 400px;
                    width: 100vw;
                    left: 0;
                }
            }
        }
    }
}